//
import Sheet from "../component/sheet";

// src/visualization/get_data.js

export function get_data(sheet) {
  try {
    const { data, selector } = sheet;

    if (!data || !selector) {
      console.error('getSelectedData: sheet ');
      return [];
    }

    // 
    const range = selector.range;
    if (!range) {
      console.error('getSelectedData: ');
      return [];
    }

    const { sri, sci, eri, eci } = range; // //

    const result = [];
    for (let r = sri; r <= eri; r++) {
      const row = [];
      for (let c = sci; c <= eci; c++) {
        const cell = data.getCell(r, c);
        if (cell) {
          row.push(cell.text || cell.value || '');
        } else {
          row.push('');
        }
      }
      result.push(row);
    }

    console.log('', result);
    return result;
  } catch (err) {
    console.error('get_data.js ', err);
    return [];
  }
}
